import React, { useState, Fragment, useEffect } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { Button } from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/DeleteOutlined';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import RestoreIcon from '@material-ui/icons/Restore';
import CenterOfAttentionForm from "./CenterOfAttentionForm";

import Tooltip from '@material-ui/core/Tooltip';
import TablePagination from '@material-ui/core/TablePagination';
import { listCenterOfAttention, deleteCenterOfAttention, restoreCenterOfAttention } from '../../api/api';

import NumberFormat from 'react-number-format';


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
    },
    iconWidth: {
        width: 10
    },
    paper: {
        padding: theme.spacing(2),
        marginTop: 10,
    },
    button: {
        margin: theme.spacing(1),
    },
    input: {
        display: 'none',
    },
    fab: {
        margin: theme.spacing(1),
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
    icon: {
        marginRight: theme.spacing(1),
        color: 'black'
    },
}))

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
})



const CenterOfAttention = () => {

    const classes = useStyles()

    const [centersOfAttention, setCentersOfAttention] = useState([])
    const [totalCentersOfAttention, setTotalCentersOfAttention] = useState(0)
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [page, setPage] = useState(0);

    const [open, setOpen] = useState(false);
    const [openRestoreRole, setOpenRestoreCenterOfAttention] = useState(false);
    const [openEditRole, setOpenEditRole] = useState(false);
    const [openCreateCenterOfAttention, setOpenCreateCenterOfAttention] = useState(false);
    const [centerOfAttention, setCenterOfAttention] = useState({})

    useEffect(() => {
        let data = { start: 0, end: rowsPerPage }
        listCenterOfAttention(data).then(response => {
            setCentersOfAttention(response.centersOfAttention);
            setTotalCentersOfAttention(response.count);
        });
    }, []);

    const onOpenDeleteCenterOfAttention = centerOfAttention => () => {
        setCenterOfAttention(centerOfAttention)
        setOpen(true);
    }
    const onOpenRestoreCenterOfAttention = role => () => {
        setCenterOfAttention(role)
        setOpenRestoreCenterOfAttention(true);
    }
    const onCloseDisableCenterOfAttention = () => {
        setOpen(false);
    }
    const onDisableCenterOfAttention = () => {
        setOpen(false);

        deleteCenterOfAttention(centerOfAttention._id).then(response => {
            if (response.status === 'success') {
                const newCentersOfAttention = centersOfAttention.map((currentCenterOfAttention) => {
                    if (centerOfAttention._id !== currentCenterOfAttention._id)
                        return currentCenterOfAttention

                    currentCenterOfAttention.status = 'inactive'
                    return currentCenterOfAttention
                });
                setCentersOfAttention(newCentersOfAttention)
            }
        });
    }
    const onRestoreCenterOfAttention = () => {

        setOpenRestoreCenterOfAttention(false)

        restoreCenterOfAttention(centerOfAttention._id).then(response => {
            if (response.status === 'success') {
                const newCentersOfAttention = centersOfAttention.map((currentRoles, index) => {

                    if (centerOfAttention._id !== currentRoles._id)
                        return currentRoles

                    currentRoles.status = 'active'
                    return currentRoles
                })
                setCentersOfAttention(newCentersOfAttention)
            }
        });
    }
    const onEditCenterOfAttention = role => () => {
        setCenterOfAttention(role)
        setOpenEditRole(true)
    };
    const onCloseEditCenterOfAttention = () => {
        setOpenEditRole(false)
    };
    const onCloseRestoreRole = () => {
        setOpenRestoreCenterOfAttention(false)
    };
    const onCreateCenterOfAttention = () => {
        setOpenCreateCenterOfAttention(true)
    }
    const onCloseCreateCenterOfAttention = () => {
        setOpenCreateCenterOfAttention(false)
    }
    const passCreateCenterOfAttention = centerOfAttention => {
        setCentersOfAttention(centerOfAttentions => [...centerOfAttentions, centerOfAttention]);
        setOpenCreateCenterOfAttention(false);
    }
    const passCenterOfAttentionEdit = centerOfAttention => {
        setOpenEditRole(false);
        const newCentersOfAttention = centersOfAttention.map((currentCenterOfAttention) => {

            if (centerOfAttention._id !== currentCenterOfAttention._id)
                return currentCenterOfAttention

            currentCenterOfAttention = centerOfAttention;
            return currentCenterOfAttention
        })
        setCentersOfAttention(newCentersOfAttention)
    }
    const handleChangePage = (e, newPage) => {
        setPage(newPage);
        let data = { start: (newPage * rowsPerPage), end: rowsPerPage }
        listCenterOfAttention(data).then(response => {
            setCentersOfAttention(response.centersOfAttention);
            setTotalCentersOfAttention(response.count);

        });
    }
    const handleChangeRowsPerPage = event => {

        setRowsPerPage(+event.target.value);
        setPage(0);
        let data = { start: 0, end: event.target.value }
        listCenterOfAttention(data).then(response => {
            setCentersOfAttention(response.centersOfAttention);
            setTotalCentersOfAttention(response.count);
        });

    }
    const onCloseModal = () => {
        setOpenCreateCenterOfAttention(false)
    }



    return (
        <Fragment>
            <Paper className={classes.paper}>
                <Button variant="contained" color="primary" onClick={onCreateCenterOfAttention}>Crear centro de atención</Button>

                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Nombre</TableCell>
                            <TableCell>Descripción</TableCell>
                            <TableCell>Cliente</TableCell>
                            <TableCell align='right'>Costo mantenimiento</TableCell>
                            <TableCell colSpan={4}>Acciones</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {
                            centersOfAttention.map(centerOfAttention => {
                                return (
                                    <TableRow key={centerOfAttention._id}>
                                        <TableCell>{centerOfAttention.title} </TableCell>
                                        <TableCell>{centerOfAttention.description} </TableCell>
                                        <TableCell>{centerOfAttention.customer.name} </TableCell>
                                        <TableCell align='right'>
                                            <NumberFormat value={centerOfAttention.maintenanceCost} displayType={'text'} thousandSeparator={true} prefix={'$'} />
                                        </TableCell>

                                        <TableCell className={classes.iconWidth}>
                                            {
                                                !centerOfAttention.administrative ?
                                                    <Tooltip title="Editar" placement="top">
                                                        <EditIcon className={classes.icon} onClick={onEditCenterOfAttention(centerOfAttention)} />
                                                    </Tooltip>
                                                    : ""
                                            }
                                        </TableCell>
                                        <TableCell className={classes.iconWidth}>
                                            {
                                                <Tooltip title="Eliminar" placement="top">
                                                    {
                                                        centerOfAttention.status === 'active' ?
                                                            <DeleteIcon className={classes.icon} onClick={onOpenDeleteCenterOfAttention(centerOfAttention)} />
                                                            :
                                                            <RestoreIcon className={classes.icon} onClick={onOpenRestoreCenterOfAttention(centerOfAttention)} />
                                                    }
                                                </Tooltip>
                                            }
                                        </TableCell>

                                    </TableRow>)
                            }
                            )}
                    </TableBody>
                </Table>
                <TablePagination
                    rowsPerPageOptions={[10, 25, 100]}
                    component="div"
                    count={totalCentersOfAttention}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>

            <Dialog
                open={open}
                onClose={onCloseDisableCenterOfAttention}
                aria-labelledby="draggable-dialog-title">
                <DialogTitle style={{ cursor: 'move' }} id="draggable-dialog-title">
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de desabilitar este centro de atención
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseDisableCenterOfAttention} color="primary">
                        NO
                    </Button>
                    <Button onClick={onDisableCenterOfAttention} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={openRestoreRole}
                onClose={onCloseRestoreRole}>
                <DialogTitle style={{ cursor: 'move' }} >
                    Alerta
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Esta seguro de reactivar a este centro de atención?
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseRestoreRole} color="primary">
                        NO
                    </Button>
                    <Button onClick={onRestoreCenterOfAttention} color="primary">
                        SI
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog maxWidth="md" open={openEditRole} onCloseEditCenterOfAttention={onCloseEditCenterOfAttention} TransitionComponent={Transition}>
                <DialogContent>
                    <CenterOfAttentionForm onClose={onCloseEditCenterOfAttention} centerOfAttention={centerOfAttention} passCenterOfAttentionEdit={passCenterOfAttentionEdit} />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={onCloseEditCenterOfAttention} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog open={openCreateCenterOfAttention} onClose={onCloseCreateCenterOfAttention} aria-labelledby="form-dialog-title">
                <DialogTitle>Crear centro de atención</DialogTitle>
                <DialogContent>
                    <CenterOfAttentionForm onCloseModal={onCloseModal} passCreateCenterOfAttention={passCreateCenterOfAttention} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={onCloseCreateCenterOfAttention} color="primary">
                        Cerrar
                    </Button>
                </DialogActions>
            </Dialog>
        </Fragment>
    )
}

export default CenterOfAttention