import React, { Fragment, useState, useEffect } from 'react';
import { emphasize, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import clsx from 'clsx';
import { Button } from '@material-ui/core';
import PropTypes from "prop-types";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Autocomplete from '@material-ui/lab/Autocomplete';
import { updateCenterOfAttention, listCustomers, createCenterOfAttention } from '../../api/api';
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Swal from 'sweetalert2';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))



const CenterOfAttentionForm = props => {

  useEffect(() => {
    console.log('props', props)

    listCustomers().then(response => {
      setCustomers(response.customers);
      if (props.centerOfAttention) {
        setCustomer(props.centerOfAttention.customer);
      }
    });
    if (props.centerOfAttention) {
      setName(props.centerOfAttention.title);
      setDescription(props.centerOfAttention.description);
      setCustomer(props.centerOfAttention.customer);

    }
  }, [])

  const [disabled, setDisabled] = useState(false)

  const classes = useStyles()

  const [customers, setCustomers] = useState([]);
  const [customer, setCustomer] = useState();
  const [description, setDescription] = useState('');
  const [name, setName] = useState('');
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [email, setEmail] = useState('');
  const [maintenanceCost, setMaintenanceCost] = useState('');

  const [errorName, setErrorName] = useState(false)
  const [errorTextName, setErrorTextName] = useState('')

  const [errorDescription, setErrorDescription] = useState(false)
  const [errorTextDescription, setErrorTextDescription] = useState('')

  const [errorUsername, setErrorUsername] = useState(false)
  const [errorTextUsername, setErrorTextUsername] = useState('')

  const [errorPassword, setErrorPassword] = useState(false)
  const [errorTextPassword, setErrorTextPassword] = useState('')

  const [errorEmail, setErrorEmail] = useState(false)
  const [errorTextEmail, setErrorTextEmail] = useState('')

  const [errorMaintenanceCost, setErrorMaintenanceCost] = useState(false)
  const [errorTextMaintenanceCost, setErrorTextMaintenanceCost] = useState('')

  // '2014-08-18T21:11:54'
  const [expirationDateMaintenance, setExpirationDateMaintenance] = useState(new Date());


  const onChangeDescription = e => {
    setDescription(e.target.value);
    validateDescription(e.target.value);
  }

  const onChangeName = e => {
    setName(e.target.value);
    validateName(e.target.value);
  }

  const onChangeUsername = e => {
    setUsername(e.target.value);
    validateUsername(e.target.value);
  }

  const onChangePassword = e => {
    setPassword(e.target.value);
    validatePassword(e.target.value);
  }

  const onChangeEmail = e => {
    setEmail(e.target.value);
    validateEmail(e.target.value);
  }

  const onChangeMaintenanceCost = e => {
    setMaintenanceCost(e.target.value);
    validateMaintenanceCost(e.target.value);
  }

  const validateForm = () => {
    let errors = 0
    errors += validateDescription()
    errors += validateName()
    errors += validateUsername()
    errors += validatePassword()

    return errors
  }

  const validateDescription = (value) => {
    let text = value !== undefined ? value : description

    if (text == null || text.toString().trim() === '') {
      setErrorDescription(true)
      setErrorTextDescription('Este campo es requerido')
      return 1
    }
    else {
      setErrorDescription(false)
      setErrorTextDescription('')
      return 0
    }
  }

  const validateName = (value) => {
    let text = value !== undefined ? value : name

    if (text == null || text.toString().trim() === '') {
      setErrorName(true)
      setErrorTextName('Este campo es requerido')
      return 1
    }
    else {
      setErrorName(false)
      setErrorTextName('')
      return 0
    }
  }

  const validateUsername = (value) => {
    let text = value !== undefined ? value : username

    if (text == null || text.toString().trim() === '') {
      setErrorUsername(true)
      setErrorTextUsername('Este campo es requerido')
      return 1
    }
    else {
      setErrorUsername(false)
      setErrorTextUsername('')
      return 0
    }
  }

  const validatePassword = (value) => {
    let text = value !== undefined ? value : password

    if (text == null || text.toString().trim() === '') {
      setErrorPassword(true)
      setErrorTextPassword('Este campo es requerido')
      return 1
    }
    else {
      setErrorPassword(false)
      setErrorTextPassword('')
      return 0
    }
  }

  const validateEmail = (value) => {
    let text = value !== undefined ? value : email

    if (text == null || text.toString().trim() === '') {
      setErrorEmail(true)
      setErrorTextEmail('Este campo es requerido')
      return 1
    }
    else {
      setErrorEmail(false)
      setErrorTextEmail('')
      return 0
    }
  }

  const validateMaintenanceCost = (value) => {
    let text = value !== undefined ? value : maintenanceCost

    if (text == null || text.toString().trim() === '') {
      setErrorMaintenanceCost(true)
      setErrorTextMaintenanceCost('Este campo es requerido')
      return 1
    }
    else {
      setErrorMaintenanceCost(false)
      setErrorTextMaintenanceCost('')
      return 0
    }
  }


  const handleDateChange = (date) => {
    setExpirationDateMaintenance(date);
  };

  const onSubmit = () => {

    let errores = validateForm()

    if (errores === 0) {

      let data = {
        name,
        description,
        username,
        password,
        expirationDateMaintenance,
        'customer': customer._id,
        maintenanceCost: parseFloat(maintenanceCost)
      }

      if (!props.centerOfAttention) {
        createCenterOfAttention(data).then(response => {

          if (response.status == 'success') {
            Swal.fire({
              icon: 'success',
              title: `El centro de atención ${name}  fue creado exitosamente`,
              showConfirmButton: false,
              timer: 1500
            });
            props.onCloseModal();
            props.passCreateCenterOfAttention(response.centerOfAttention);
          } else {
            Swal.fire({
              icon: 'error',
              title: `Ocurrió un error al crear el centro de atención ${name}`,
              showConfirmButton: false,
              timer: 1500
            })
          }

        });

      } else {

        data.id = props.centerOfAttention._id;
        updateCenterOfAttention(data).then(response => {
          if (response.status == 'success') {
            Swal.fire({
              icon: 'success',
              title: `El centro de atención ${name}  fue actualizado exitosamente`,
              showConfirmButton: false,
              timer: 1500
            });
            props.onClose();
            props.passCenterOfAttentionEdit(response.centerOfAttention);
          } else {
            Swal.fire({
              icon: 'error',
              title: `Ocurrió un error al actualizar el centro de atención ${name}`,
              showConfirmButton: false,
              timer: 1500
            })
          }

        });
      }
    } else {
      toast.error('🦄 Wow so easy!', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  }


  return (
    <Fragment>
      <ToastContainer />
      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <form encType={'multipart/form-data'}
            onSubmit={e => {
              e.preventDefault()
              onSubmit()
            }} >
            <Grid container spacing={3}>

              <Grid item xs={12} sm={12}>
                <Autocomplete
                  fullWidth
                  size="small"
                  getOptionLabel={
                    (option) => {
                      return `${option.name.toString().toUpperCase()} `
                    }
                  }
                  filterOptions={(x) => x}
                  options={customers}
                  // value={customer}
                  // defaultValue={customers.length > 0 ? customers[1] : null}

                  autoComplete
                  getOptionSelected={(option, value) => option === value}
                  onChange={(_, value) => {
                    setCustomer(value);
                  }}
                  onInputChange={(_, newInputValue) => {
                    if (newInputValue.toString().trim().length > 0) {
                      let data = {
                        search: newInputValue.toString().trim()
                      }
                      listCustomers(data).then(response => {
                        setCustomers(response.customers);
                      })
                    }
                  }}
                  renderInput={(params) => (
                    <TextField {...params} label="Cliente" variant="outlined" margin="dense" />
                  )}
                  renderTags={(tagValue, getTagProps) =>
                    tagValue.map((option, index) => (
                      <Chip
                        label={option.name}
                        {...getTagProps({ index })}
                        disabled={customers.indexOf(option) !== -1}
                      />
                    ))
                  }
                  renderOption={(option) => {

                    return (
                      <Grid container alignItems="center">
                        <Grid item xs>
                          <Typography >{`${option.name.toString().toUpperCase()}`}</Typography>
                        </Grid>
                      </Grid>
                    );
                  }}
                />
              </Grid>

              <Grid item xs={12} sm={12}>
                <TextField
                  fullWidth
                  error={errorName}
                  helperText={errorTextName}
                  margin="dense"
                  autoComplete='off'
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Nombre del centro de atención"
                  value={name}
                  onChange={onChangeName} />
              </Grid>


              <Grid item xs={12} sm={12}>
                <TextField
                  fullWidth
                  error={errorDescription}
                  helperText={errorTextDescription}
                  margin="dense"
                  autoComplete='off'
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Descripción del centro de atención"
                  value={description}
                  onChange={onChangeDescription} />
              </Grid>

              <Grid item xs={12} sm={6}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    style={{ width: '100%' }}
                    margin="normal"
                    label="Vencimiento mantenimiento"
                    format="MM/dd/yyyy"
                    value={expirationDateMaintenance}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                  />
                </MuiPickersUtilsProvider>
              </Grid>

              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  error={errorMaintenanceCost}
                  helperText={errorMaintenanceCost}
                  margin="dense"
                  autoComplete='off'
                  className={clsx(classes.margin, classes.textField)}
                  variant="outlined"
                  label="Costo del mantenimiento"
                  value={maintenanceCost}
                  onChange={onChangeMaintenanceCost} />
              </Grid>

              <Grid item xs={12} sm={12}>

                {
                  !props.centerOfAttention ? <fieldset>
                    <legend><Typography>Datos del oset</Typography></legend>
                    <Grid container spacing={1}>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          error={errorUsername}
                          helperText={errorTextUsername}
                          margin="dense"
                          autoComplete='off'
                          className={clsx(classes.margin, classes.textField)}
                          variant="outlined"
                          label="Nombre de usuario"
                          value={username}
                          onChange={onChangeUsername} />
                      </Grid>
                      <Grid item xs={12} sm={6}>
                        <TextField
                          fullWidth
                          error={errorPassword}
                          helperText={errorTextPassword}
                          margin="dense"
                          autoComplete='off'
                          className={clsx(classes.margin, classes.textField)}
                          variant="outlined"
                          label="Contraseña"
                          value={password}
                          onChange={onChangePassword} />
                      </Grid>
                      <Grid item xs={12} sm={12}>
                        <TextField
                          fullWidth
                          error={errorEmail}
                          helperText={errorTextEmail}
                          margin="dense"
                          autoComplete='off'
                          className={clsx(classes.margin, classes.textField)}
                          variant="outlined"
                          label="Correo electrónico"
                          value={email}
                          onChange={onChangeEmail} />
                      </Grid>
                    </Grid>
                  </fieldset>
                    : ''
                }
              </Grid>

              <Grid item xs={2} sm={3}>
                <Button disabled={disabled} type="submit" variant="contained" color='primary'>
                  {props.centerOfAttention ? "Actualizar" : "Guardar"}
                </Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default CenterOfAttentionForm